// tabs
.quicktabs-tabs {
  margin-bottom: 30px;
  display: flex;
  align-items: center;
  gap: 10px;

  li {
      box-shadow: 0 0 20px 0 rgba(22, 31, 48, 0.1);
      border-radius: 4px;
      font-family: var(--font-family);
      font-size: 16px;
      line-height: 100%;
      font-weight: 700;
      transition: all 0.4s ease-in-out;

      a {
          padding: 12px 18px;
      }

      &:hover,
      &.active {
          color: rgba(var(--cl-a),1.0);
          background-color: rgba(var(--bg-e),1.0);
          box-shadow: none;
      }
  }
}

